<template>
  <div>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-aside width="200px" :default-active="defActive" style="background-color: rgb(238, 241, 246)">
            <el-menu :router="true">
              <el-submenu index="1">
                  <template slot="title"><i class="el-icon-message"></i>主页</template>
                  <el-menu-item-group>
                    <el-menu-item index="1-1" @click="develop">开发常用记录</el-menu-item>
                  </el-menu-item-group>
              </el-submenu>
              <el-submenu index="2">
                  <template slot="title"><i class="el-icon-menu"></i>练习</template>
                  <el-menu-item-group>
                    <el-menu-item index="2-1" @click="upload">文件上传</el-menu-item>
                    <el-menu-item index="2-2" @click="tabShow">列表展示</el-menu-item>
                  </el-menu-item-group>
              </el-submenu>
            </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      defActive: ""
    };
  },
  created() {
    this.defActive = this.$storage.get("defActive");
  },
  methods: {
    develop() {
      this.$router.push("/my");
      this.$storage.set("defActive", "1-1");
    },
    upload() {
      this.$router.push("/ele/index");
      this.$storage.set("defActive", "2-1");
    },
    tabShow() {
      this.$router.push("/ele/container");
      this.$storage.set("defActive", "2-2");
    }
  }
};
</script>

<style>
</style>



